import { Field, Form, Formik } from 'formik';
import React, { Component } from 'react'
import { connect } from 'react-redux';
import { withRouter } from 'react-router'
import { changeNoteAction } from 'store/creators';

export class EditNote extends Component {
  constructor(props) {
    super(props);
    this.index = this.props.match.params.index;
    this.note = this.props.notes[this.index];
  }

  render() {
    const n = this.note;

    return (
      <div>
        <h1 style={{ textAlign: 'center' }}>Edit Note:</h1>
        <Formik
          initialValues={{
            title: n.title,
            tags: n.tags.join(' '),
            done: n.done
          }}
          onSubmit={(v) => {
            const newNote = {
              title: v.title,
              done: v.done,
              tags: v.tags.split(' ')
            }
            const payload = {
              index: this.index,
              newNote
            }
            this.props.changeNote(payload);
            this.props.history.push('/home');
          }}
        >
          <Form style={{ width: '400px', margin: '0 auto' }}>
            <div className="input-field">
              <Field
                name="title"
                id="title"
                placeholder="Title"
              />
            </div>
            <div className="input-field">
              <Field
                name="tags"
                id="tags"
                placeholder="List tags with spaces"
              />
            </div>
            <div className="input-field">
              <label>
                <Field
                  name="done"
                  id="indeterminate-checkbox"
                  type="checkbox"
                />
                <span> - done</span>
              </label>
            </div><br/><br/><br/>
            <button type="submit" className="btn blue darken-4">
              <i class="material-icons right">
                edit
              </i>
              Change
            </button>
          </Form>
        </Formik>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  notes: state
})

const mapDispatchToProps = (dispatch) => {
  return {
    changeNote(payload) {
      dispatch(changeNoteAction(payload))
    }
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(EditNote));
