import Note from 'components/Note';
import React, { Component } from 'react'
import { connect } from 'react-redux'

export class Home extends Component {
  constructor(props) {
    super(props);
    this.notes = props.notes;
  }

  renderedNotes = () => {
    const rendered = this.notes.map(note => (
      <Note
        key={this.notes.indexOf(note)}
        index={this.notes.indexOf(note)}
        title={note.title}
        tags={note.tags}
        done={note.done}
      />
    ));
    return rendered;
  };

  render() {
    const rendered = this.props.notes.map(note => (
      <Note
        key={this.props.notes.indexOf(note)}
        index={this.props.notes.indexOf(note)}
        title={note.title}
        tags={note.tags}
        done={note.done}
      />
    ));

    return (
      <div>
        <h1>Notes:</h1>
        <table className="striped z-depth-1 responsive-table">
          <thead style={{ fontWeight: 'bold' }}>
            <tr>
              <td>#</td>
              <td>Title</td>
              <td>Tags</td>
              <td>Done</td>
              <td>Edit</td>
              <td>Delete</td>
            </tr>
          </thead>
          <tbody>
            {/* {this.renderedNotes()} */}
            {rendered}
          </tbody>
        </table>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  notes: state
})

const mapDispatchToProps = {
  
}


export default connect(mapStateToProps, mapDispatchToProps)(Home);
