import { ADD_NOTE, CHANGE_NOTE, DELETE_NOTE } from "./types";

const { default: initial } = require("./initial");

function reducer(state = initial, action) {
  let copy = [...state];
  switch (action.type) {
    case DELETE_NOTE:
      const { index } = action.payload;
      copy.splice(index, 1);
      return copy;
    case ADD_NOTE:
      copy.push(action.payload);
      return copy;
    case CHANGE_NOTE:
      copy[action.payload.index] = action.payload.newNote;
      return copy;
  }
  return state;
}

export default reducer;
