const { DELETE_NOTE, ADD_NOTE, CHANGE_NOTE } = require("./types");

export function deleteNoteAction(payload) {
  return {
    type: DELETE_NOTE,
    payload
  };
}

export function addNoteAction(payload) {
  return {
    type: ADD_NOTE,
    payload
  };
}

export function changeNoteAction(payload) {
  return {
    type: CHANGE_NOTE,
    payload
  };
}
