import { Field, Form, Formik } from 'formik'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { addNoteAction } from 'store/creators'

export class Create extends Component {
  render() {
    return (
      <div>
        <h2 style={{ textAlign: 'center' }}>
          Create Note:
        </h2>
        <Formik
          initialValues={{
            title: '',
            tags: ''
          }}
          validate={(v) => {
            const errors = {};
            if (
              v.title.trim() === '' ||
              v.tags.trim() === ''
            ) {
              errors.message = 'Empty value';
            }
            return errors;
          }}
          onSubmit={(v) => {
            const payload = {
              title: v.title,
              tags: v.tags.split(' ')
            };
            this.props.addNote(payload);
            this.props.history.push('/home');
          }}
        >
          <Form style={{ width: '400px', margin: '0 auto' }}>
            <div className="input-field">
              <Field
                id="title"
                placeholder="Title"
                name="title"
              />
            </div>
            <div className="input-field">
              <Field
                id="tags"
                placeholder="List tags with spaces"
                name="tags"
              />
            </div>
            <button type="submit" className="btn blue darken-4">
              <i class="material-icons right">add</i>
              Create
            </button>
          </Form>
        </Formik>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps = (dispatch) => {
  return {
    addNote(payload) {
      dispatch(addNoteAction(payload))
    }
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Create));
