import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import { deleteNoteAction } from 'store/creators';

export class Note extends Component {
  handleDeleteNote = () => {
    const payload = { index: this.props.index };
    this.props.deleteNote(payload);
  };

  render() {
    const {
      title,
      tags,
      done,
      index
    } = this.props;

    return (
      <tr>
        <td>
          {index + 1}
        </td>
        <td style={{ fontSize: '20px' }}>
          {title}
        </td>
        <td>
          {tags.map(tag => (
            <span
              className="badge grey darken-4 red-text text-lighten-5"
              key={Math.random()}
              style={{
                margin: '0 5px', borderRadius: '10px'}}
            >
              {tag}
            </span>
          ))}
        </td>
        <td>
          {done ? (
            <i
              className="material-icons green-text"
            >
              done
            </i>
          ) : (
            <i className="material-icons red-text">
              close
            </i>
          )}
        </td>
        <td>
          <Link to={'/note/' + index} className="btn green">
            Edit
          </Link>
        </td>
        <td>
          <button className="btn red accent-4" onClick={this.handleDeleteNote}>
            Delete
          </button>
        </td>
      </tr>
    )
  }
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps = (dispatch) => {
  return {
    deleteNote(payload) {
      dispatch(deleteNoteAction(payload));
    }
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Note))
